.background-about {
  @media screen and (min-width: 1280px) {
    background-image: url(../images/background/background_desktop/background_spoon_desktop.png);
    background-repeat: no-repeat;
  }
}

// back top
.bg-top {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  background-repeat: no-repeat;
  z-index: -2;
  width: 100%;
  height: 460px;
  background-size: 100% 460px;
  background-image: url(../images/background/background_mobile/background_top_mobile.png);
}

@media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .bg-top {
    background-image: url(../images/background/background_mobile/background_top_mobile_2x.png);
  }
}

@media (min-width: 768px) and (max-width: 1279px) {
  .bg-top {
    height: 622px;
    background-size: 100% 622px;
    background-image: url(../images/background/background_tablet/background_top_tablet.png);
  }
  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    .bg-top {
      background-image: url(../images/background/background_tablet/background_top_tablet_2x.png);
    }
  }
}

@media (min-width: 1280px) {
  .bg-top {
    height: 1035px;
    background-size: 100% 1035px;
    background-image: url(../images/background/background_desktop/background_top_desktop.png);
  }
  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    .bg-top {
      background-image: url(../images/background/background_desktop/background_top_desktop_2x.png);
    }
  }
}

