// donut top
.donut {
  position: absolute;
  display: block;
  background-repeat: no-repeat;
  top: 0;
  left: 0;
  width: 120px;
  height: 114px;
  z-index: -1;
  background-size: cover;
  background-image: url(../images/mobile/donuts_mobile.png);
}
@media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .donut {
    background-image: url(../images/mobile/donuts_mobile_2x.png);
  }
}

@media (min-width: 768px) {
  .donut {
    top: 0;
    right: 100px;
    width: 380px;
    height: 361px;
    left: auto;
    background-image: url(../images/tablet/donuts_tablet.png);
  }
  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    .donut {
      background-image: url(../images/tablet/donuts_tablet_2x.png);
    }
  }
}

@media (min-width: 1280px) {
  .donut {
    top: 0;
    right: 0;
    width: 716px;
    height: 661px;
    background-image: url(../images/desktop/donuts_desktop.png);
  }
  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    .donut {
      background-image: url(../images/desktop/donuts_desktop_2x.png);
    }
  }
}

